import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import matter from 'gray-matter'; // Import gray-matter to handle frontmatter
import './App.css';

function App() {
  const [posts, setPosts] = useState([]);
  const [postContent, setPostContent] = useState(null);
  const [selectedLanguage, setSelectedLanguage] = useState('en');
  const [selectedPost, setSelectedPost] = useState(null); // No default post, controlled by URL

  // Function to get the query parameter value by name
  const getQueryParam = (param) => {
    const urlParams = new URLSearchParams(window.location.search);
    return urlParams.get(param);
  };

  // Handle language and post from query parameters on initial load
  useEffect(() => {
    const lang = getQueryParam('lang') || 'en'; // Default to 'en' if no lang is specified
    const postSlug = getQueryParam('post'); // Post slug from URL

    setSelectedLanguage(lang);
    if (postSlug) {
      setSelectedPost(postSlug); // Set post from URL if available
    } else {
      setSelectedPost('about_me'); // Default to 'about_me' if no post is specified
    }

    // Fetch posts.json for the selected language
    fetch(`/posts/${lang}/posts.json`)
      .then(response => response.json())
      .then(data => setPosts(data))
      .catch(error => console.error(`Error loading posts for ${lang}:`, error));
  }, []);

  // Fetch post content based on the selected language and post slug
  useEffect(() => {
    if (selectedPost) {
      fetch(`/posts/${selectedLanguage}/${selectedPost}.md`)
        .then(response => response.text())
        .then(text => {
          const { content } = matter(text); // Parse the Markdown and get the content (without frontmatter)
          setPostContent(content); // Set the post content without the frontmatter
        })
        .catch(error => console.error(`Error loading post content for ${selectedPost}:`, error));
    }
  }, [selectedPost, selectedLanguage]);

  // Handle click on post title
  const loadPost = (slug) => {
    setSelectedPost(slug);
    window.history.pushState(null, '', `?lang=${selectedLanguage}&post=${slug}`); // Update the URL without reloading
  };

  return (
    <div className="App">
      <header>
        <h1>Evan d'Entremont</h1>

        <h2>Opinionated thoughts on tech</h2>

        <blockquote>This site is under development</blockquote>
        <nav>
          {/* Language Selection */}
          <button onClick={() => setSelectedLanguage('en')}>English</button>
          <button onClick={() => setSelectedLanguage('fr')}>Français</button>
          <button onClick={() => setSelectedLanguage('test')}>Test</button>
        </nav>
      </header>

      <main>
        <section className="post-content">
          {postContent ? (
            <ReactMarkdown>{postContent}</ReactMarkdown>
          ) : (
            <p>Select a post to read.</p>
          )}
        </section>

        <section className="post-list">
          <h2>All Posts</h2>
          <ul>
            {posts.map((post) => (
              <li key={post.slug}>
                <a href={`#${post.slug}`} onClick={() => loadPost(post.slug)}>
                  {post.title}
                </a>
              </li>
            ))}
          </ul>
        </section>
      </main>

      <footer>
        <p>Blog powered by React</p>
      </footer>
    </div>
  );
}

export default App;